.products {
  padding-top: 121px;
  padding-bottom: 98px;
}
.product__container {
  padding-bottom: 0;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
  max-width: 1280px;
}
.products__block {
  margin-bottom: 31px;
  @media screen and (min-width: 768px) {
    margin-bottom: 39px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.13;
    margin-bottom: 49px;
  }
}

.products__label {
  font-family: 'Titan One';
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
  margin-bottom: 15px;

  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.products__main__title {
  font-family: 'Titan One';
  font-size: 34px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #d41443;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: 1.14;
    margin-bottom: 39px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 58px;
    line-height: 1.14;
    margin-bottom: 49px;
  }
}


.products__item {
  position: relative;
    &:hover,
    &:focus {
      border: 3px solid #ffffff;
    }
  align-items: center;
  @media screen and (max-width: 767px) {
    margin: 0 auto;
    &:not(:first-child) {
      margin-top: calc(113px + 17px);
    }
  }

  @media screen and (min-width: 320px) and (max-width: 479px) {
    width: 100%;
  }

  height: 485px;
  background: #ffb8ca;
  border-radius: 24px;

  @media screen and (min-width: 480px) {
    align-items: center;
    width: 280px;
  }

  @media screen and (min-width: 768px) and(max-width:1199px) {
    width: 220px;
    &:not(:first-child) {
      margin-left: 20px;
    }
    flex-basis: calc(100% / 3 - 40px);
    margin-top: 104px;
  }

  @media screen and (min-width: 1200px) {
    width: 329px;
    height: 525px;
    &:not(:first-child) {
      margin-left: 30px;
    }
    margin-top: 210px;
    width: calc(100% / 3 - 60px);
  }
}

.products__list {
  position: relative;

  @media screen and (max-width: 767px) {
    min-height: 1714px;
    padding-top: 113px;
  }


  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -20px;
 
  }

  @media screen and (min-width: 1200px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -30px;
  }
}
.products__title--ice-cream {

  &:hover,
  &:focus {
    text-shadow: 1px 1px 2px black;
  }
}
.products__title--ice-cream::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-272px);
  content: url(../images/products/ice-cream-mob.png);

  @media screen and (min-width: 768px) {
    content: url(../images/products/ice-cream-tab.png);
    transform: translateY(-246px);
  }

  @media screen and (min-width: 1200px) {
    content: url(../images/products/ice-cream-desk.png);

    transform: translateY(-370px);
  }
}
.products__title--ice-coffee {

  &:hover,
  &:focus {
    text-shadow: 1px 1px 2px black;
  }
}
.products__title--ice-coffee::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-272px);
  content: url(../images/products/ice-coffe-mob.png);

  @media screen and (min-width: 768px) {
    content: url(../images/products/ice-coffe-tab.png);
    transform: translateY(-260px);
  }

  @media screen and (min-width: 1200px) {
    content: url(../images/products/ice-coffe-desk.png);
    transform: translateY(-370px);
  }
}
.products__title--milkshakes {

  &:hover,
  &:focus {
    text-shadow: 1px 1px 2px black;
  }
}
.products__title--milkshakes::before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-272px);
  content: url(../images/products/milkshakes-mob.png);
  margin-left: 16px;
  margin-right: 15px;

  @media screen and (min-width: 768px) {
    content: url(../images/products/milkshakes-tab.png);
    margin-left: 0;
    margin-right: 0;
    transform: translateY(-241px);
  }

  @media screen and (min-width: 1200px) {
    content: url(../images/products/milkshakes-desk.png);
    transform: translateY(-370px);
  }
}

.products__title::after {
  content: url(../images/etc.png);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(30px);
}
.product__item--ice-cream {

  &:hover,
  &:focus {
    box-shadow: 13px 13px 13px rgba(225, 121, 146, 1),
      0px 1px 1px rgba(225, 121, 146, 1),
      0px 2px 1px rgba(225, 121, 146, 1);
    transform: scale(1.05);
  }

}
.products__item--coffee {
  background-color: #f0d1a5;
    &:hover,
    &:focus {
      box-shadow: 13px 13px 13px rgb(185, 150, 100),
        0px 1px 1px rgba(240, 209, 165, 1),
        0px 2px 1px rgba(240, 209, 165, 1);
      transform: scale(1.05);
    }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
  }
}

.products__item--milkshakes {
  background-color: #c2e297;
    &:hover,
    &:focus {
      box-shadow: 13px 13px 13px rgb(162, 197, 117),
        0px 1px 1px rgba(194, 226, 151, 1),
        0px 2px 1px rgba(194, 226, 151, 1);
      transform: scale(1.05);
    }
  @media screen and (min-width: 320px) and (max-width: 767px) {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
  }
}

.products__title {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(161px);
  font-family: 'Titan One';
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #ffffff;

  @media screen and (min-width: 768px) {
    transform: translateY(152px);
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: 1.13;
  }
}

.products__text {
  position: relative;
  transform: translateY(205px);
  font-family: 'DM Sans';
  font-weight: 700;
  font-size: 14px;
  line-height: 1.52;
  text-align: center;
  margin-bottom: 30px;
  letter-spacing: 0.04em;
  color: #ffffff;
  margin: 0 auto;
  width: 200px;

  @media screen and (min-width: 768px) {
    display: block;
    line-height: 1.78;
    width: 198px;
    margin: 0 auto;
    transform: translateY(210px);
  }

  @media screen and (min-width: 1200px) {
    display: block;
    font-size: 16px;
    line-height: 1.75;
    width: 241px;
    margin: 0 auto;
  }
}

.buttons__products {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #ffffff;
  cursor: pointer;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 40px;
  position: absolute;
  &:focus {
    transform: rotate(-90deg);
  }
}



.products__about {
  display: inline-flex;
  font-family: 'DM Sans';
  font-weight: 500;
  font-size: 14px;
  line-height: 1.5;
  color: #ffffff;
  padding-top: 10px;
  text-align: start;
}
