.footer{
    background-color: var(--secondary-bg-color);
}

.container__footer{
width: 100%;
padding-right: 20px;
padding-left: 20px;
margin: 0 auto;

@media screen and (min-width: 480px) {
    width: 480px;
}

@media screen and (min-width: 768px) {
    width: 768px;
    padding-right: 32px;
    padding-left: 32px;
}

@media screen and (min-width: 1280px) {
    width: 1280px;
    padding-right: 16px;
    padding-left: 16px;
}
}

.footer-section{
    padding-bottom: 43px;
    @media screen and (min-width:768px) {
    display: flex;
    flex-direction: column;
    padding-bottom: 91px;
    }
    @media screen and (min-width:1280px) {
        display: flex;
        padding-bottom: 97px;
    }

}

.top-line{
    display: block;
    
    @media screen and(min-width:768px) {
        display: flex;
        justify-content: space-between;
    }
}
// footer-title
.footer-title{
    width: 234px;
    display: inline-block;
    font-family: 'Titan One';
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 43px;
    @media screen and (min-width:768px){
        width: 340px;
        margin-bottom: 0;
    }
    @media screen and (min-width:1280px){
        width: 474px;
    }
    &__first{
        font-size: 18px;
        line-height: 1.17;
        margin-bottom: 17px;
        color: #FFA5BA;
        @media screen and (min-width:768px) {
            font-size: 26px;
            line-height: 1.15;
            letter-spacing: 0.04em;
            margin-bottom: 16px;
        }
        @media screen and (min-width:1280px) {
            font-size: 30px;
            line-height: 1.13;
            letter-spacing: 0.04em;
        }
    }
    &__second{
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    color: var(--title-color);
    
    @media screen and (min-width:768px) {
        font-size: 48px;
        line-height: 1.15;
        letter-spacing: 0.06em;
        
    }
    @media screen and (min-width:1280px){
        font-size: 58px;
        line-height: 1.14;
        letter-spacing: 0.05em;
        
        
    }

}    
}

//  footer-social
.social{
    margin-bottom: 46px;
}
.social-list{
    display: flex;
    justify-content: flex-end;
    &__link{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 34px;
        height: 34px;
        border-radius: 50%;
        background-color: #FEA5BB;
        fill:#fff;
        transition: background-color  var(--animation-timing-function);
        &:hover,
        &:focus{
        background-color: var(--title-color);
        }
    }
}
.social-list__item+.social-list__item{
    margin-left: 28px;
}

//  footer-contact
.footer-contact{
    font-family: 'DM Sans';
    font-weight: 700;
    font-size: 12px;
    line-height: 1.83em;
    text-align: right;
    
    @media screen and (min-width:768px) {
        font-size: 14px;
        line-height: 1.86;
        margin-bottom: 0;
    }
    &__tel{
        text-decoration: none;
        color: var(--title-color);
    }
}

// footer comment
.copyright{
    margin: 0 auto;
    &::before{
        content: '';
        display: block;
        border-bottom: 1px solid #E1E1E1;
    }
    &__comment{
        display: block;
    font-family: 'DM Sans';
    color: var(--second-text-color);
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5em;
    letter-spacing: 0.02em;
    text-align: center;
    width: 150px;
    margin: 0 auto;
    padding: 22px 0px 25px;
    @media screen and(min-width:768px) {
        line-height: 1.86;
        width: 276px;
        padding: 29px 0 0;
    }
    @media screen and (min-width:1280px) {
        padding-top: 9px;
        padding: 9px 0 0;
    }
}
}








